class FormStore {

    constructor() {
        this.store = {}
    }
    
    addStore(name,data) {
        this.store[name] = data
        console.log('输出store', this.store[name])
    }

    releaseStore (name){
        this.store[name] = {}
    }

    changeStoreData (name,key,value){
        this.store[name]['dataSource'][key] = value
    }

    getFormData (name,key) {
        return this.store[name]['dataSource'][key]
    }
}


export default new FormStore